.DiaryEditor {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 100px 0 70px;
  border-bottom: 1px solid #ccc;
  box-sizing: border-box;
}

.DiaryEditor .title {
  margin-top: 0;
  margin-bottom: 25px;
}

.DiaryEditor .group {
  display: flex;
  align-items: flex-start;
  width: 700px;
}

.DiaryEditor .box:last-child {
  margin-left: auto;
}

.DiaryEditor .input {
  display: block;
  width: 500px;
  padding: 6px 8px;
  box-sizing: border-box;
  border-width: 0 0 2px;
  border-bottom: 2px solid #ccc;
  font-size: 20px;
  font-weight: 700;
}

.DiaryEditor .guide {
  display: none;
  margin: 5px 0 0 5px;
  font-size: 12px;
  color: #f00;
}

.DiaryEditor .guide.is_active {
  display: block;
}

.DiaryEditor .textarea {
  display: block;
  width: 500px;
  margin-top: 20px;
  padding: 10px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 5px;
  resize: none;
}

.DiaryEditor .select {
  display: block;
  width: 100%;
  margin-top: 15px;
  padding: 3px;
  border-radius: 2px;
  border: 1px solid #ddd;
}

.DiaryEditor .btn_submit {
  display: block;
  width: 100%;
  box-sizing: border-box;
  margin-top: 15px;
  padding: 9px;
  background: rgba(255,215,215,.7);
  border: 1px solid transparent;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color .25s;
}

.DiaryEditor .btn_submit:hover {
  background: rgba(255,215,215,1);
}

/* Item */
.DiaryList {
  padding: 50px 0;
}

.DiaryList .title {
  margin: 0;
  text-align: center;
}

.DiaryList .title_desc {
  margin-top: 15px;
  margin-bottom: 0;
  text-align: center;
}

.DiaryItemGroup {
  margin: 30px 40px 0;
  padding: 30px 0;
  border-top: 2px solid #ededed;
}

.DiaryItem {
  margin-top: 15px;
  padding: 20px;
  background-color: rgba(0,0,0,.05);
}

.DiaryItem:first-child {
  margin-top: 0;
}

.DiaryItem .info_box {
  display: flex;
  margin-bottom: 20px;
}

.DiaryItem .info {
  display: block;
  font-size: 13px;
}

.DiaryItem .date {
  display: block;
  margin-left: auto;
  font-size: 13px;
  color: rgba(0,0,0,.5);
}

.DiaryItem .content {
  margin: 0 0 10px;
  font-size: 14px;
}

.DiaryItem .content .textarea {
  display: block;
  width: 100%;
  min-height: 65px;
  padding: 5px;
  box-sizing: border-box;
  resize: none;
}

.DiaryItem .btn {
  display: inline-block;
  margin-left: 5px;
  padding: 3px 6px;
  border: 1px solid #acacac;
  border-radius: 5px;
  font-size: 13px;
  color: rgba(0,0,0,.5);
  vertical-align: top;
  cursor: pointer;
  transition: background-color .25s, color .25s;
}

.DiaryItem .btn:hover {
  background-color: #ddd;
  color: #111;
}

.DiaryItem .btn:first-of-type {
  margin-left: 0;
}
